import React from 'react';
import autoskolaLogo from './autoskolaLogo.svg';

import {Link} from "react-router-dom";

const Login = ({onInputChange, isAuthenticated}) => { 
    return (
      
      <div className="App">
        <header className="App-header">
          <img src={autoskolaLogo} alt="logo" />
          <p>
            Prometni propisi i sigurnosna pravila
          </p>
          <input
          type='password'
          name='password'
          className='bg-light-blue w-30 ma2' 
          placeholder='Unesi lozinku'
          onChange={onInputChange}
          />
          {
          isAuthenticated === true ? 
            <Link to='/page'>
            <button
            className="f5 bg-green br-pill grow ba bw2 ph3 pv1">
              Pristupi
            </button>
            </Link> :
            <Link to='/'>
            <button
            className="f5 bg-green br-pill grow ba bw2 ph3 pv1">
              Pristupi
            </button>
            </Link>
          }
        </header>
      </div>
    );
}

export default Login;
