import React, { Component } from 'react';
import Login from './components/Login';
import Page from './components/Page';
import './App.css';
import 'tachyons';

import {
  HashRouter,
  Switch,
  Route,
} from "react-router-dom";

class App extends Component {
  constructor() {
    super();
    this.state = {
      isAuthenticated: false
    }
  }

 onInputChange = (event) => {
    const password = "bruno";
    if (event.target.value === password) {
      this.setState({ isAuthenticated: true });
    }
  }

  onLogoutClick = () => {
      this.setState({ isAuthenticated: false });
  }

  render () {
    return (
          <HashRouter basename='/'>
            <div>
            <Switch>
              <Route exact path="/">
                <Login onInputChange = {this.onInputChange} isAuthenticated={this.state.isAuthenticated} />
              </Route>
              <Route exact path="/page/">
                <Page onLogoutClick = {this.onLogoutClick}/>
              </Route>
            </Switch>
          </div>
        </HashRouter>
    );
  }
}

export default App;